import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/home/home'
import Health from 'components/health/health'
import HealthDetail from 'components/health/health-detail'
import Mine from 'components/mine/mine'
import Care from 'components/care/health-care'
import CareCenter from 'components/care/care-center'
import Physical from 'components/physical/physical'
import PhysicalDetail from 'components/physical/physical-detail'
import SubmitOrder from 'components/order/submit-order'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/health',
      name: 'health',
      component: Health,
      children: [
        {
          path: ':id',
          component: HealthDetail
        }
      ]
    },
    {
      path: '/mine',
      name: 'mine',
      component: Mine
    },
    {
      path: '/care',
      name: 'care',
      component: Care
    },
    {
      path: '/careCenter',
      name: 'careCenter',
      component: CareCenter
    },
    {
      path: '/physical',
      name: 'physical',
      component: Physical,
      children: [
        {
          path: ':id',
          component: PhysicalDetail
        }
      ]
    },
    {
      path: '/submitOrder',
      name: 'submitOrder',
      component: SubmitOrder
    }
  ]
})



