
import Vue from 'vue'
import HeaderBack from 'components/base/header-back'
import Scroll from 'components/base/scroll'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Group from 'vux/src/components/group/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import good1 from 'common/images/goods-swipe1.png'
import good2 from 'common/images/goods-swipe2.png'
Vue.use(ToastPlugin)
const baseList = [{
url: 'javascript:',
img: good1
}, {
url: 'javascript:',
img: good2
}]
export default {
components: {Scroll, HeaderBack, Swiper, SwiperItem, XNumber, Group, Checker, CheckerItem},
data () {
return {
title: '商品详情',
numTitle: '购买数量',
isActive: false,
demo06_list: baseList,
color: '',
size: ''
}
},
created() {
this.$watch('color', (newColor) => {
this.color = newColor
})
},
methods: {
layerOpen() {
this.isActive = true
},
layerClose() {
this.isActive = false
},
addShopCard() {
if (this.color) {
this.$vux.toast.show({
type: 'text',
text: '可以添加购物车',
time: '1000',
width: '20rem',
position: 'middle'
})
} else {
this.$vux.toast.show({
type: 'text',
text: '请选择商品规格',
time: '1000',
width: '20rem',
position: 'middle'
})
}
},
buy() {
this.$router.push({path: `/submitOrder`})
}
}
}



