
import Scroll from 'components/base/scroll'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import axios from 'axios'
const baseList = [{
url: 'javascript:',
img: 'https://static.vux.li/demo/2.jpg',
title: '送你一朵fua'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/1.jpg',
title: '送你一辆车'
}]
export default {
components: {Swiper, SwiperItem, Scroll},
data () {
return {
demo06_list: baseList,
sliderList: []
}
},
created() {
this._initBanner()
},
methods: {
goUrl(id) {
this.$router.push({path: `/health/${id}`
})
},
_initBanner () {
axios.get('/v1/title/banner')
.then(function (response) {
console.log(response)
})
.catch(function (error) {
console.log(error)
})
}
}
}



