
import Scroll from 'components/base/scroll'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

const baseList = [{
url: 'javascript:',
img: 'https://static.vux.li/demo/1.jpg',
title: '送你一朵fua'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/3.jpg',
title: '送你一次旅行',
fallbackImg: 'https://static.vux.li/demo/3.jpg'
}]
export default {
components: { Swiper, SwiperItem, Scroll },
data () {
return {
demo06_list: baseList
}
},
methods: {
goUrl(page) {
if (page === 1) {
this.$router.push({path: `/care`})
} else if (page === 2) {
this.$router.push({path: `/physical`})
}
}
}
}



